

body {
  margin: 0;
  padding: 0;
  font-family: "Italiana", sans-serif; 
  background-color: #333333; 
  color: #e0e0e0; 
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.App {
  width: 100%; 
  height: 100%; 
  display: flex; 
  justify-content: center; 
  align-items: center; 
}

.App-header {
  background-color: rgba(255, 255, 255, 0.2); 
  backdrop-filter: blur(50px); 
  padding: 40px 20px; 
  border-radius: 10px;
  width: 350px; 
  box-shadow: 0 4px 15px rgba(87, 46, 12, 0.5);
  text-align: center;
}

h1 {
  color: #ff8c00; 
  margin-bottom: 20px; 
}

input {
  width: calc(100% - 12px); 
  padding: 10px;
  border: 2px solid #bd6902; 
  border-radius: 5px;
  margin-bottom: 10px; 
  background-color: rgba(255, 255, 255, 0.1); 
  color: #ffffff; 
  box-shadow: 0 4px 15px rgba(87, 46, 12, 0.5);
}

button {
  background-color: #ff8c00; 
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 0; 
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s; 
  box-shadow: 0 4px 15px rgba(87, 46, 12, 0.5);
}

button:hover {
  background-color: #cc7a00; 
}

.weather-info {
  margin-top: 20px; 
  text-align: center; 
  display: flex; 
  flex-direction: column; 
  align-items: center; 
}

.weather-info p {
  margin: 5px 0; 
}
